* {
  box-sizing: border-box;
}

:root {
  --bg-main: #0b0b12;
  --bg-surface: #12121c;
  --bg-code: #181827;
  --text-main: #e8e6f0;
  --text-muted: #9a9a9a;
  --text-body: #d6d6d6;
  --accent: #b9a3ff;
  --accent-soft: #b9a3ff59;
  --accent-glow: #b9a3ff2e;
  --border: #b9a3ff26;
  color-scheme: dark;
}

@media (prefers-color-scheme: light) {
  :root {
    --bg-main: #f6f5fb;
    --bg-surface: #fff;
    --bg-code: #f0eef8;
    --text-main: #1a1824;
    --text-muted: #6f6f7a;
    --text-body: #2a2836;
    --accent: #7b5cff;
    --accent-soft: #7b5cff59;
    --accent-glow: #7b5cff2e;
    --border: #1a18241f;
    color-scheme: light;
  }
}

body {
  background: var(--bg-main);
  color: var(--text-main);
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
}

.wrapper {
  max-width: 700px;
  margin: 100px auto;
  padding: 0 20px;
}

h1 {
  color: var(--text-main);
  margin-bottom: 6px;
  font-size: 34px;
  font-weight: 600;
}

.subtitle {
  color: var(--text-muted);
  margin-bottom: 40px;
}

h2 {
  color: var(--accent);
  margin-top: 48px;
  margin-bottom: 14px;
  font-size: 20px;
  font-weight: 500;
}

p, li {
  color: var(--text-body);
  font-size: 16px;
  line-height: 1.75;
}

ul {
  padding-left: 18px;
}

code {
  background: var(--bg-code);
  color: var(--text-main);
  white-space: pre-wrap;
  border: 1px solid var(--border);
  border-radius: 6px;
  margin: 18px 0;
  padding: 14px;
  font-family: ui-monospace, SFMono-Regular, monospace;
  font-size: 14px;
  display: block;
}

.link {
  color: var(--accent);
  border-bottom: 1px solid var(--accent-soft);
  margin-top: 50px;
  padding-bottom: 2px;
  text-decoration: none;
  transition: color .15s, border-color .15s;
  display: inline-block;
}

.link:hover {
  color: var(--accent);
  border-color: var(--accent);
}

.license {
  color: var(--text-muted);
  margin-top: 40px;
  font-size: 14px;
}

@keyframes focusPulse {
  0% {
    box-shadow: 0 0 0 1px var(--accent-soft);
  }

  50% {
    box-shadow: 0 0 0 1px var(--accent), 0 0 8px var(--accent-glow);
  }

  100% {
    box-shadow: 0 0 0 1px var(--accent-soft);
  }
}

code:hover {
  animation: 2.8s ease-in-out infinite focusPulse;
}

@media (prefers-reduced-motion: reduce) {
  code:hover {
    animation: none;
  }
}
